html,
body {
  height: 100%;
  scroll-behavior: smooth;
}

.body {
  font-family: "PT Sans", "sans-serif";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.7;
  color: var(--main-color-text);
  background: #ffffff;
  min-width: 280px;
  max-width: 1920px;
  margin-inline: auto;
  overflow-x: hidden;
}

.body--lock {
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.visibility-hidden {
  opacity: 0;
  visibility: hidden;
  font-size: 0;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}

.container {
  max-width: 1200px;
  padding-inline: 15px;
  margin-inline: auto;
}

.scroll-top {
  position: fixed;
  bottom: 55px;
  right: 15px;
  width: 50px;
  height: 50px;
  border: 1px solid var(--border-color);
  border-radius: 50%;
  background-color: rgba(102, 102, 102, 0.4);
  display: none;
  align-items: center;
  justify-content: center;
}

.scroll-top::after {
  content: "";
  pointer-events: none;
  position: absolute;
  left: 30%;
  top: 35%;
  width: 20px;
  height: 20px;
  border-top: 4px solid var(--white-color);
  border-right: 4px solid var(--white-color);
  transform: rotate(-45deg);
}

.header {
  padding-block: 34px;
}

.header__inner {
  max-width: 1550px;
  padding-inline: 15px;
  margin-inline: auto;
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav__list {
  display: flex;
  align-items: center;
  gap: 35px;
}

.nav__item {
  font-family: "PT Serif", "sans-serif";
  line-height: 19px;
}

.nav__link {
  color: var(--main-color-text);
}

.burger-menu {
  border: none;
  background-color: transparent;
  display: block;
  height: 28px;
  width: 40px;
  position: relative;
  cursor: pointer;
  z-index: 21;
}

.burger-menu span {
  position: absolute;
  top: calc(50% - 1px);
  left: 0;
  width: 100%;
  height: 3px;
  background-color: var(--main-color-text);
  transition: all 0.2s ease 0s;
}

.burger-menu span:first-child {
  top: 0;
}

.burger-menu span:last-child {
  top: auto;
  bottom: 0;
}

.burger-menu--active span {
  background-color: var(--white-color);
  transform: scale(0);
}

.burger-menu--active span:first-child {
  transform: rotate(-45deg);
  top: calc(50% - 1px);
}

.burger-menu--active span:last-child {
  transform: rotate(45deg);
  bottom: calc(50% - 1px);
}

.hero {
  margin-bottom: 60px;
  position: relative;
  height: 700px;
}

.hero::before {
  content: "";
  position: absolute;
  z-index: -1;
  background-image: url(../images/hero-bg.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.hero::after {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  right: 0;
  bottom: 0;
  height: 300px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.000065) 0%, rgba(0, 0, 0, 0.65) 100%);
}

.hero__inner {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 59px;
  color: var(--white-color);
  position: relative;
}

.hero__content {
  width: 100%;
}

.hero__suptitle {
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  margin-bottom: 4px;
}

.hero__title {
  font-weight: 700;
  font-size: 40px;
  line-height: 125%;
  margin-bottom: 15px;
}

.hero__text {
  max-width: 700px;
}

.articles {
  margin-bottom: 80px;
}

.articles__inner {
  display: flex;
  justify-content: space-between;
  gap: 130px;
}

.articles__list {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 30px;
}

.articles__item {
  grid-column: span 3;
  max-width: 370px;
}

.articles__pagination {
  margin-top: 60px;
}

.article {
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
}

.article__info {
  text-align: center;
  padding: 15px 42px 15px;
}

.article__category {
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  color: var(--accent-color);
  margin-bottom: 4px;
}

.article__title {
  font-family: "PT Serif";
  font-size: 20px;
  line-height: 28px;
  color: var(--main-color-text);
  margin-bottom: 10px;
}

.article__desc {
  font-family: "PT Serif";
  font-size: 12px;
  line-height: 16px;
  color: var(--second-color-text);
  display: flex;
  justify-content: center;
  gap: 12px;
}

.article__desc .by {
  color: var(--additional-color-text);
}

.article__text {
  font-family: "PT Sans";
  color: var(--second-color-text);
  margin-top: 14px;
}

.article-big {
  grid-column: span 6;
  max-width: 100%;
}

.article-big .article__info {
  text-align: left;
  padding: 25px 126x 25px 31px;
}

.article-big .article__desc {
  justify-content: start;
}

.pagination {
  max-width: 680px;
  margin-inline: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  padding: 18px 25px 16px 25px;
}

.pagination__button {
  font-family: "HK Grotesk";
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  border: none;
  background-color: transparent;
}

.pagination__button--off {
  color: rgba(23, 23, 23, 0.4);
}

.pagination__button--off svg {
  stroke-opacity: 0.4;
}

.pagination__prev svg,
.pagination__next svg {
  stroke: #171717;
}

.pagination__prev svg {
  margin-right: 10px;
}

.pagination__next svg {
  margin-left: 10px;
}

.pagination__pages {
  display: flex;
  gap: 41px;
  font-family: "HK Grotesk";
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
}

.pagination__page {
  cursor: pointer;
}

.aside {
  max-width: 270px;
}

.aside__item:not(:last-child) {
  margin-bottom: 70px;
}

.aside__item-title {
  border: 1px solid var(--border-color);
  font-family: "PT Serif";
  font-size: 16px;
  line-height: 21px;
  text-align: center;
  padding-block: 15px 14px;
  margin-bottom: 20px;
}

.aside-author {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
}

.aside-author__image {
  margin-bottom: 20px;
}

.aside-author__info {
  text-align: center;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 14px;
}

.aside-author__name {
  font-family: "PT Serif";
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
}

.aside-author__theme {
  font-family: "PT Serif";
  font-style: italic;
  line-height: 19px;
  color: var(--additional-color-text);
  margin-bottom: 14px;
}

.aside-author__about {
  color: var(--second-color-text);
}

.aside-author__more {
  text-align: center;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  color: var(--accent-color);
  padding-block: 12px;
}

.aside-arcticles article {
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
}

.categories__item {
  padding-block: 14px;
}

.categories__item:first-child {
  padding-top: 0;
}

.categories__item:not(:first-child) {
  border-top: 1px solid var(--border-color);
}

.categories__link {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "PT Serif";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: var(--second-color-text);
}

.social__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3px;
}

.social__item {
  grid-column: span 1;
  width: 88px;
  height: 88px;
}

.social__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: var(--second-color-text);
}

.social__link svg {
  margin-bottom: 6px;
  width: 20px;
  height: 20px;
}

.tags__list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.tags__item {
  font-family: "PT Serif";
  font-size: 14px;
  line-height: 19px;
  color: var(--second-color-text);
  padding: 6px 15px;
}

.models {
  padding-inline: 15px;
  margin-bottom: 100px;
}

.models__title {
  font-family: "PT Serif";
  font-size: 24px;
  line-height: 32px;
  text-align: center;
  margin-bottom: 39px;
}

.models__title a {
  color: var(--main-color-text);
}

.models__inner {
  display: flex;
  gap: 21px;
}

.models__image {
  grid-column: span 1;
  max-width: 296px;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__logo {
  display: block;
  margin-bottom: 59px;
}

.footer__nav-list {
  display: flex;
  align-items: center;
  gap: 35px;
  margin-bottom: 30px;
}

.footer__nav-item {
  font-family: "PT Serif", "sans-serif";
  line-height: 19px;
}

.footer__nav-link {
  color: var(--second-color-text);
}

.footer__socials {
  display: flex;
  gap: 20px;
  margin-bottom: 40px;
}

.footer__socials-item {
  cursor: pointer;
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.footer__socials-item:hover svg {
  fill: var(--background-hover-icons);
}

.footer__socials-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid var(--border-color);
  background-color: #fff;
}

.footer__socials-item:hover::before {
  border: 1px solid #fff;
  background-color: var(--accent-color);
}

.footer__socials-item svg {
  fill: var(--background-icons);
}

.footer__copyright {
  padding-bottom: 10px;
  color: var(--second-color-text);
}

@media (min-width: 769px) {
  .burger-menu {
    display: none;
  }
}

@media (max-width: 1366px) {
  .hero {
    height: 600px;
  }
}

@media (max-width: 1024px) {
  .hero {
    height: 550px;
    margin-bottom: 30px;
  }

  .articles__inner {
    gap: 50px;
  }

  .articles__item {
    grid-column: span 6;
    max-width: 100%;
  }

  .article {
    display: flex;
  }

  .article__info {
    padding: 35px 15px 15px;
  }

  .article-big .article {
    display: block;
  }

  .models__inner {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 30px;
  }

  .models__image {
    grid-column: span 2;
  }
}

@media (max-width: 960px) {
  .articles__pagination {
    margin-top: 20px;
  }

  .pagination {
    flex-direction: column;
  }

  .pagination__button {
    border: 1px solid var(--border-color);
    width: 100%;
    padding: 10px;
  }

  .pagination__prev {
    text-align: left;
  }

  .pagination__next {
    text-align: right;
  }
}

@media (max-width: 860px) {
  .articles__item {
    grid-column: span 3;
    max-width: 370px;
  }

  .article {
    flex-direction: column;
  }

  .article__info {
    padding: 15px;
  }

  .article-big {
    grid-column: span 6;
    max-width: 100%;
  }

  .aside {
    display: none;
  }

  .models {
    margin-bottom: 40px;
  }

  .models__inner {
    gap: 10px;
  }

  .footer__logo {
    margin-bottom: 30px;
  }
}

@media (max-width: 768px) {
  .body {
    font-size: 18px;
  }

  .nav__list {
    width: 0px;
    transform: translateY(-400px);
    transition: transform 0.4s ease;
  }

  .nav__list--active {
    padding-top: 130px;
    flex-direction: column;
    gap: 5px;
    overflow-y: auto;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 20;
    width: 100%;
    height: 100vh;
    background-color: var(--accent-color);
    transform: translateY(0px);
  }

  .nav__list--active .nav__item {
    width: 100%;
  }

  .nav__list--active .nav__item a {
    display: block;
    text-align: center;
    font-size: 28px;
    line-height: 30px;
    color: var(--white-color);
    padding-block: 20px;
  }

  .nav__item {
    font-size: 0;
  }

  .nav__item:hover {
    color: var(--second-color-text);
  }
}

@media (max-width: 540px) {
  .body {
    font-size: 16px;
  }

  .hero::after {
    height: 430px;
  }

  .articles__item {
    grid-column: span 6;
    max-width: 100%;
  }

  .article__image {
    width: 100%;
  }

  .article__image img {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .models__image {
    grid-column: span 3;
  }
}

@media (max-width: 440px) {
  .footer__nav-list {
    gap: 20px;
    flex-direction: column;
  }

  .footer__socials {
    gap: 12px;
    margin-bottom: 30px;
  }
}

@media (max-width: 400px) {
  .article__title {
    font-size: 18px;
    line-height: 22px;
  }
}

@media (any-hover: hover) {
  .nav__item:hover .nav__link {
    color: var(--accent-color);
  }

  .footer__nav-item:hover .footer__nav-link {
    color: var(--accent-color);
  }
}