.header {
    padding-block: 34px;
    &__inner {
        max-width: 1550px;
        padding-inline: 15px;
        margin-inline: auto;
    }
    &__nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

.nav {
    &__list {
        display: flex;
        align-items: center;
        gap: 35px;

        @media (max-width: 768px) {
            width: 0px;
            transform: translateY(-400px);
            transition: transform 0.4s ease;
            &--active {
              padding-top: 130px;
              flex-direction: column;
              gap: 5px;
              overflow-y: auto;
              position: absolute;
              top: 0;
              left: 0;
              z-index: 20;
              width: 100%;
              height: 100vh;
              background-color: var(--accent-color);
              transform: translateY(0px);
              .nav__item {
                width: 100%;
              }
              .nav__item a {
                display: block;
                text-align: center;
                font-size: 28px;
                line-height: 30px;
                color: var(--white-color);
                padding-block: 20px;
              }
            }
          }
    }
    &__item {
        font-family: "PT Serif", "sans-serif";
        line-height: 19px;
        @media (any-hover: hover) {
            &:hover {
                .nav__link {
                    color: var(--accent-color);
                }
            }
        }

        @media (max-width: 768px) {
            font-size: 0;
            &:hover {
              color: var(--second-color-text);
            }
          }
    }
    &__link {
        color: var(--main-color-text);
    }
}

.burger-menu {
    border: none;
    background-color: transparent;
    display: block;
    height: 28px;
    width: 40px;
    position: relative;
    cursor: pointer;
    z-index: 21;
    @media (min-width: 769px) {
      display: none;
    }
    span {
      position: absolute;
      top: calc(50% - 1px);
      left: 0;
      width: 100%;
      height: 3px;
      background-color: var(--main-color-text);
      transition: all 0.2s ease 0s;
      &:first-child {
        top: 0;
      }
      &:last-child {
        top: auto;
        bottom: 0;
      }
    }
    &--active {
      span {
        background-color: var(--white-color);
        transform: scale(0);
        &:first-child {
          transform: rotate(-45deg);
          top: calc(50% - 1px);
        }
        &:last-child {
          transform: rotate(45deg);
          bottom: calc(50% - 1px);
        }
      }
    }
  }