.hero {
    margin-bottom: 60px;
    position: relative;
    height: 700px;
    @media (max-width: 1366px) {
        height: 600px;
    }
    @media (max-width: 1024px) {
        height: 550px;
        margin-bottom: 30px;
    }
    
    &::before {
        content: "";
        position: absolute;
        z-index: -1;
        background-image: url(../images/hero-bg.jpg);
        background-repeat: no-repeat;
        background-position: top center;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
    &::after {
        content: "";
        position: absolute;
        z-index: -1;
        left: 0;
        right: 0;
        bottom: 0;
        height: 300px;
        background: linear-gradient(180deg, rgba(0, 0, 0, 6.5e-05) 0%, rgba(0, 0, 0, 0.65) 100%);

        @media (max-width: 540px) {
            height: 430px;
        }
    }

    &__inner {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        padding-bottom: 59px;
        color: var(--white-color);
        position: relative;
    }

    &__content {
        width: 100%;
    }

    &__suptitle {
        font-size: 14px;
        line-height: 18px;
        text-transform: uppercase;
        margin-bottom: 4px;
    }

    &__title {
        font-weight: 700;
        font-size: 40px;
        line-height: 125%;
        margin-bottom: 15px;
    }

    &__text {
        max-width: 700px;
    }
}
