@import 'fonts';
html,body {
    height: 100%;
    scroll-behavior: smooth;
}
.body {
    font-family: "PT Sans", "sans-serif";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.7;
    color: var(--main-color-text);
    background: #ffffff;
    min-width: 280px;
    max-width: 1920px;
    margin-inline: auto;
    overflow-x: hidden;
    @media (max-width: 768px) {
        font-size: 18px;
    }
    @media (max-width: 540px) {
        font-size: 16px;
    }
}
.body--lock {
    overflow: hidden;
}

img {
    max-width: 100%;
    height: auto;
}

.visibility-hidden {
    opacity: 0;
    visibility: hidden;
    font-size: 0;
}

.wrapper{
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

.main {
    flex: 1 1 auto;
}

.container {
    max-width: 1200px;
    padding-inline: 15px;
    margin-inline: auto;
}

.scroll-top {
    position: fixed;
    bottom: 55px;
    right: 15px;
    width: 50px;
    height: 50px;
    border: 1px solid var(--border-color);
    border-radius: 50%;
    background-color: rgba(#666666, .4);
    display: none;
    align-items: center;
    justify-content: center;
    // @media (any-hover: hover) {
    //     &:hover {
    //         color: var(--accent-dark-color);
    //     }
    // }

    &::after {
        content: "";
        pointer-events: none;
        position: absolute;
        left: 30%;
        top: 35%;
        width: 20px;
        height: 20px;
        border-top: 4px solid var(--white-color);
        border-right: 4px solid var(--white-color);
        transform: rotate(-45deg);
    }
}