.aside {
    max-width: 270px;
    @media (max-width: 860px) {
        display: none;
    }

    &__item:not(:last-child) {
        margin-bottom: 70px;
    }

    &__item-title {
        border: 1px solid var(--border-color);
        font-family: "PT Serif";
        font-size: 16px;
        line-height: 21px;
        text-align: center;
        padding-block: 15px 14px;
        margin-bottom: 20px;
    }

}

.aside-author {
    display: flex;
    flex-direction: column;
    box-shadow: (0px 2px 6px rgba(0, 0, 0, 0.1));

    &__image {
        margin-bottom: 20px;
    }

    &__info {
        text-align: center;
        display: flex;
        flex-direction: column;
        border-bottom: 1px solid var(--border-color);
        padding-bottom: 14px;
    }

    &__name {
        font-family: "PT Serif";
        font-weight: 700;
        font-size: 16px;
        line-height: 21px;
    }

    &__theme {
        font-family: "PT Serif";
        font-style: italic;
        line-height: 19px;
        color: var(--additional-color-text);
        margin-bottom: 14px;
    }

    &__about {
        color: var(--second-color-text);
    }

    &__more {
        text-align: center;
        font-size: 12px;
        line-height: 16px;
        text-transform: uppercase;
        color: var(--accent-color);
        padding-block: 12px;
    }
}

.aside-arcticles {
    article {
        box-shadow: (0px 2px 4px rgba(0, 0, 0, 0.1));
        margin-bottom: 30px;
    }
}


.categories {

    &__item {
        padding-block: 14px;
    }
    &__item:first-child {
        padding-top: 0;
    }
    &__item:not(:first-child) {
        border-top: 1px solid var(--border-color);
    }

    &__link {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-family: "PT Serif";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        color: var(--second-color-text);
    }
}

.social {
    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 3px;
    }
    &__item {
        grid-column: span 1;
        width: 88px;
        height: 88px;
    }
    &__link {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: 12px;
        line-height: 16px;
        text-align: center;
        color: var(--second-color-text);

        svg {
            margin-bottom: 6px;
            width: 20px;
            height: 20px;
        }
    }
}

.tags {
    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
    }

    &__item {
        font-family: "PT Serif";
        font-size: 14px;
        line-height: 19px;
        color: var(--second-color-text);
        padding: 6px 15px;
    }
}
