.footer {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__logo {
        display: block;
        margin-bottom: 59px;
        @media (max-width: 860px) {
            margin-bottom: 30px;
        }
    }
    &__nav-list {
        display: flex;
        align-items: center;
        gap: 35px;
        margin-bottom: 30px;
        @media (max-width: 440px) {
            gap: 20px;
            flex-direction: column;
        }
    }
    &__nav-item {
        font-family: "PT Serif", "sans-serif";
        line-height: 19px;
        @media (any-hover: hover) {
            &:hover {
                .footer__nav-link {
                    color: var(--accent-color);
                }
            }
        }
    }
    &__nav-link {
        color: var(--second-color-text);
    }
    &__socials {
        display: flex;
        gap: 20px;
        margin-bottom: 40px;
        @media (max-width: 440px) {
            gap: 12px;
            margin-bottom: 30px;
        }
    }
    &__socials-item {
        cursor: pointer;
        width: 34px;
        height: 34px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        &:hover {
            svg {
                fill: var(--background-hover-icons);
            }
        }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            border: 1px solid var(--border-color);
            background-color: #fff;
        }
        &:hover::before {
            border: 1px solid #fff;
            background-color: var(--accent-color);
        }

        

        svg {
            fill: var(--background-icons);
        }
    }

    &__copyright {
        padding-bottom: 10px;
        color: var(--second-color-text);
    }
}
