.models {
    padding-inline: 15px;
    margin-bottom: 100px;
    @media (max-width: 860px) {
        margin-bottom: 40px;
    }

    &__title {
        font-family: "PT Serif";
        font-size: 24px;
        line-height: 32px;
        text-align: center;
        margin-bottom: 39px;

        a {
            color: var(--main-color-text);
        }
    }
    &__inner {
        display: flex;
        gap: 21px;
        @media (max-width: 1024px) {
            display: grid;
            grid-template-columns: repeat(6, 1fr);
            column-gap: 30px;
        }
        @media (max-width: 860px) {
            gap: 10px;
        }
    }
    &__image {
        grid-column: span 1;
        max-width: 296px;
        @media (max-width: 1024px) {
            grid-column: span 2;
        }
        @media (max-width: 480px) {
            grid-column: span 3;
        }
    }
}
