.articles {
    margin-bottom: 80px;

    &__inner {
        display: flex;
        justify-content: space-between;
        gap: 130px;
        @media (max-width: 1024px) {
            gap: 50px;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        gap: 30px;
    }

    &__item {
        grid-column: span 3;
        max-width: 370px;
        @media (max-width: 1024px) {
            grid-column: span 6;
            max-width: 100%;
        }
        @media (max-width: 860px) {
            grid-column: span 3;
            max-width: 370px;
        }
        @media (max-width: 540px) {
            grid-column: span 6;
            max-width: 100%;
        }
    }

    &__pagination {
        margin-top: 60px;
        @media (max-width: 960px) {
            margin-top: 20px;
        }
    }
}
.article {
    box-shadow: (0px 2px 6px rgba(0, 0, 0, 0.1));
    @media (max-width: 1024px) {
        display: flex;
    }
    @media (max-width: 860px) {
        flex-direction: column;
    }

    &__image {
        @media (max-width:540px) {
            width: 100%;
            
            img {
                width: 100% ;
            }
        }
    }

    &__info {
        text-align: center;
        padding: 15px 42px 15px;

        @media (max-width: 1024px) {
            padding: 35px 15px 15px;
        }

        @media (max-width: 860px) {
            padding: 15px;
        }
    }

    &__category {
        font-size: 12px;
        line-height: 16px;
        text-transform: uppercase;
        color: var(--accent-color);
        margin-bottom: 4px;
    }

    &__title {
        font-family: "PT Serif";
        font-size: 20px;
        line-height: 28px;
        color: var(--main-color-text);
        margin-bottom: 10px;
        @media (max-width: 400px) {
            font-size: 18px;
            line-height: 22px;
        }
    }

    &__desc {
        font-family: "PT Serif";
        font-size: 12px;
        line-height: 16px;
        color: var(--second-color-text);
        display: flex;
        justify-content: center;
        gap: 12px;

        .by {
            color: var(--additional-color-text);
        }
    }

    &__text {
        font-family: "PT Sans";
        color: var(--second-color-text);
        margin-top: 14px;
    }
}

.article-big {
    grid-column: span 6;
    max-width: 100%;
    @media (max-width: 1024px) {
        .article {
            display: block;
        }
    }
    @media (max-width: 860px) {
        grid-column: span 6;
        max-width: 100%;
    }

    .article__info {
        text-align: left;
        padding: 25px 126x 25px 31px;
    }

    .article__desc {
        justify-content: start;
    }
}

.pagination {
    max-width: 680px;
    margin-inline: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    padding: 18px 25px 16px 25px;
    @media(max-width: 960px) {
        flex-direction: column;
    }

    &__button {
        font-family: "HK Grotesk";
        font-size: 14px;
        line-height: 16px;
        text-transform: uppercase;
        border: none;
        background-color: transparent;
        @media (max-width: 960px) {
            border: 1px solid var(--border-color);
            width: 100%;
            padding: 10px;
        }

        &--off {
            color: rgba(#171717, 0.4);
            svg {
                stroke-opacity: 0.4;
            }
        }
    }

    &__prev,
    &__next {
        svg {
            stroke: #171717;
        }
    }

    &__prev {
        @media (max-width: 960px) {
            text-align: left;
        }

        svg {
            margin-right: 10px;
        }
    }

    &__next {
        @media (max-width: 960px) {
            text-align: right;
        }

        svg {
            margin-left: 10px;
        }
    }

    &__pages {
        display: flex;
        gap: 41px;
        font-family: "HK Grotesk";
        font-size: 16px;
        line-height: 16px;
        text-transform: uppercase;
    }

    &__page {
        cursor: pointer;
    }
}
